import React, { useEffect, useState } from 'react';
import './button.scss'
import { SCSS, useThemeContext } from '../contexts/theme-context';
import { isDisabled } from '@testing-library/user-event/dist/utils';
import { useLogContext } from '../contexts/log-context';
import { MessageType } from '../Types';
interface ButtonProps {
  onClick?: () => void,
  color: ButtonColor,
  text: string,
  isSelected?: boolean,
  isDisabled?: boolean,
  disabledMessage?: string,
}

export enum ButtonColor { BLUE, RED, GREEN, YELLOW, WHITE }

const Button: React.FC<ButtonProps> = ({onClick,disabledMessage, text, isSelected, isDisabled, color}) => {
  const { scss } = useThemeContext();
  const { popMessage } = useLogContext();
  // const [isClicking, setIsClicking] = useState<boolean>(false);
  
  let count = 0;

  const getTheme = () => {
    let rtn = '';
    if(isDisabled || !onClick) return ' button-bg-disabled ';

    switch(color){
      case ButtonColor.BLUE:
        return ' button-bg-blue' +( isSelected?'-selected ':' ') + ' button-container-active';
      case ButtonColor.RED:
        return ' button-bg-red button-container-active';
      case ButtonColor.GREEN:
        return ' button-bg-green button-container-active';
      case ButtonColor.YELLOW:
        return ' button-bg-yellow button-container-active';
      case ButtonColor.WHITE:
        return ' button-bg-white button-container-active';
    }
  }

  const click = () => {
    if(!onClick)return;

    if(isDisabled){
      count +=1;
      setTimeout(() => {
        count += 0;
      }, 3000);

      if(disabledMessage){
        popMessage(disabledMessage, MessageType.Alert);
      }
      else if(count >= 3 && !disabledMessage){
        popMessage(`It's off......`, MessageType.Alert);
      }
    }
    else{
      onClick();
    }
  }

  return (
    <div 
      className={'button-container ' + getTheme()}
      onClick={click}
      // onMouseDown={()=>setIsClicking(true)}
      // onMouseUp={()=>setIsClicking(false)}
      >
      {text}
    </div>
  );
};

export default Button;